
import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router";
import Spinner from "../ChatApp/UI/Spinner/Spinner";

import {
    Container,
    Card,
    CardContent,
    makeStyles,
    Button,
    TextField
} from "@material-ui/core";
import Alert from "../ChatApp/UI/Alert";


const SignIn = () => {

    const [name, setName] = useState('');
    const [lastName, setLastName] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [image, setImage] = useState('');
    const [error, setError] = useState(false);
    const [errorMessage, setErrorMessage] = useState('');
    const [spinner, setSpinner] = useState(false);

    const navigate = useNavigate();

    const onsubmitHandler = async () => {

        if (name === '' || email === '' || password === '' || lastName === '') {
            setErrorMessage("All fields are required");
            setError(true);
            setTimeout(() => {
                setError(false);
            }, 5000);
            return;
        }
        try {
            setSpinner(true);
            const formData = new FormData();
            formData.append('name', name);
            formData.append('lastName', lastName);
            formData.append('email', email);
            formData.append('password', password);
            formData.append('file', image);

            const result = await axios.post('/api/signup', formData);
            setSpinner(false);
            if (result.status === 201) {
                navigate('/signin');
            }
        } catch (error) {
            setSpinner(false);
            setErrorMessage(error.response.data.message);
            setError(true);
            setTimeout(() => {
                setError(false);
                setErrorMessage('')
            }, 8000);
        }


    }



    const classess = useStyle();
    return (
        <div>
            {
                spinner ? <Spinner /> : ''
            }
            <Container className={classess.container}>
                <Card>
                    <CardContent className={classess.content}>
                        {error ? <Alert message={errorMessage} /> : null}
                        <h1>Sign Up</h1>
                        <form className={classess.root} noValidate autoComplete="off" onSubmit={onsubmitHandler} encType="multipart/form-data">
                            <TextField
                                required
                                id="outlined-name-required"
                                label="Enter Name"
                                variant="outlined"
                                value={name}
                                onChange={(event) => setName(event.target.value)}
                            />
                            <TextField
                                required
                                id="outlined-lastName-required"
                                label="Enter Last Name"
                                variant="outlined"
                                value={lastName}
                                onChange={(event) => setLastName(event.target.value)}
                            />
                            <TextField
                                required
                                id="outlined-email-required"
                                label="Enter Email"
                                variant="outlined"
                                value={email}
                                onChange={event => setEmail(event.target.value)}
                            />
                            <TextField
                                required
                                id="outlined-password-input"
                                label="Enter Password"
                                type="password"
                                variant="outlined"
                                value={password}
                                onChange={event => setPassword(event.target.value)}
                            />
                            <TextField
                                required
                                id="outlined-input"
                                type="file"
                                variant="outlined"
                                onChange={event => setImage(event.target.files[0])}
                            />
                            <Button
                                onClick={onsubmitHandler}
                                variant="contained"
                                color="primary"
                                className={classess.button}>
                                Submit
                            </Button>
                            <Link to='/signin'>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    className={classess.button}>
                                    sign in
                                </Button>
                            </Link>
                        </form>
                    </CardContent>
                </Card>
            </Container>
        </div>
    )
}

const useStyle = makeStyles((theme) => ({
    container: {
        margin: 'auto',
        marginTop: 30,
        width: 400,
    },
    content: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        flexDirection: 'column',
        paddingLeft: 3,
    },
    root: {
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            width: '100%',
        },
    },
    button: {
        margin: 'auto',
        marginLeft: 24,
        marginBottom: 7,
        width: '90%',
    }
}))


export default SignIn;