import React, { useState } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import { Container, Card, CardContent, makeStyles, Button, TextField } from "@material-ui/core";
import { useNavigate } from "react-router";
import Alert from "../ChatApp/UI/Alert";
import Spinner from "../ChatApp/UI/Spinner/Spinner";


const SignIn = ({ history }) => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState(false);
    const [errorMessage, setErrorMessage] = useState('');
    const [spinner, setSpinner] = useState(false);
    const classess = useStyle();
    const navigate = useNavigate();


    const onSubmitHandler = async () => {
        if (email === " " || password === "") {
            setErrorMessage('All fields are required');
            setError(true);
            setTimeout(() => {
                setError(false);
            }, 5000);

            return;
        }
        setSpinner(true);
        const user = {
            email: email,
            password: password,
        }
        try {
            const result = await axios.post('/api/user/login', user);
            localStorage.setItem('user', JSON.stringify(result.data));
            localStorage.setItem('token', result.data.token);
            setSpinner(false);
            if (result.status === 200) {
                navigate('/');
            }
        } catch (error) {
            setSpinner(false);
            setErrorMessage(error.response.data.message);
            setError(true);
            setTimeout(() => {
                setError(false);
                setErrorMessage('');
            }, 8000);
        }
    }

    return (
        <div>
            {
                spinner ? <Spinner /> : ''
            }
            <Container className={classess.container}>
                <Card>
                    <CardContent className={classess.content}>
                        {
                            error ? <Alert message={errorMessage} /> : null
                        }
                        <h1 className={classess.title}>Sign in</h1>
                        <form className={classess.root} noValidate autoComplete="off">
                            <div>
                                <TextField
                                    required
                                    id="outlined-required"
                                    type="email"
                                    label="Enter Email"
                                    variant="outlined"
                                    value={email}
                                    onChange={event => setEmail(event.target.value)}
                                />
                                <TextField
                                    required
                                    id="outlined-password-input"
                                    label="Password"
                                    type="password"
                                    variant="outlined"
                                    value={password}
                                    onChange={event => setPassword(event.target.value)}
                                />
                                <Button
                                    onClick={onSubmitHandler}
                                    className={classess.button}
                                    variant="contained"
                                    color="primary">
                                    Submit
                                </Button>
                                <Link to='/signup'>
                                    <Button
                                        className={classess.button}
                                        variant="contained"
                                        color="primary">
                                        sign up
                                    </Button>
                                </Link>
                            </div>
                        </form>
                    </CardContent>
                </Card>
            </Container>
        </div>
    )

}

const useStyle = makeStyles((theme) => ({
    container: {
        margin: 'auto',
        marginTop: 100,
        width: 400,
    },
    content: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        flexDirection: 'column',
        paddingLeft: 3,
    },
    root: {
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            width: '100%',
        },
    },
    button: {
        margin: 'auto',
        marginLeft: 24,
        width: '90%',
        marginBottom: 7,
    }
}))


export default SignIn;