import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import User from "../../Users/User";
import SmallAppBar from "./smallAppBar";
import List from "./List";
import axios from "axios";
import openSocket from "socket.io-client";
import { useNavigate } from 'react-router';
import Spinner from "./Spinner/Spinner";
import AppBar from "./AppBar";
import Alert from "./Alert";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 3,
    justifyContent: 'center',
    alignItems: 'center',
    flexDirection: "column",
    padding: 100,
  },
  paper: {
    height: 450,
    width: 350,
    justifyContent: "center",
    overflow: "auto",
    border: "1px solid #BF00FF"
  },
  control: {
    padding: theme.spacing(2),
  },

  input: {
    padding: '10px 20px',
    width: '65%'
  },

  button: {
    color: 'white',
    background: 'black',
    border: 'none',
    outline: 'none',
    padding: '10px',
    marginLeft: '3px',
    border: '1px solid black'
  },

  messageDiv: {
    overflow: 'auto',
    height: "95%",
  },
  withPercent: {
    width: "100%"
  }
}));

const SpacingGrid = (props) => {
  const [spacing, setSpacing] = React.useState(2);
  const [newMessage, setNewMessage] = useState('');
  const [chat, setChat] = useState([]);
  const [request, setRequest] = useState([]);
  const [spinner, setSpinner] = useState(false);
  const [pageRefresh, setPageRefresh] = useState([]);
  const [errorMessage, setErrorMessage] = useState('');
  const [error, setError] = useState(false);
  const classes = useStyles();
  const navigate = useNavigate();


  const refreshMessages = () => {
    setInterval(() => {
      // Get Element
      const paperDiv = document.getElementById('paperDiv');


      paperDiv.scrollTop = paperDiv.scrollHeight;
    }, 2000);
  };

  // Set the div to bottom for the first time as well
  refreshMessages();




  useEffect(() => {
    setSpinner(true);
    const isAuth = localStorage.getItem('token') ? localStorage.getItem('token') : false;
    if (!isAuth) {
      return navigate('/signin')
    }
    axios.post('/api/auth', { token: isAuth })
      .then(result => {
        setSpinner(false);
        if (!result.data.isAuth) {
          return navigate('/signin');
        }
      })
      .catch(error => {
        setSpinner(false);
        return navigate('/signin');
      })

  }, [pageRefresh]);

  useEffect(() => {
    const socket = openSocket('/');
    socket.on('messages', data => {
      if (data.action === "newmessage") {
        setRequest(['1']);
      }
    })
  }, []);

  useEffect(() => {
    // Auto scroll to bottom every 2 seconds:-




    axios.get('/api/getmessages')
      .then(response => {
        setChat([...response.data]);
        refreshMessages()
      })
      .catch(error => {
        setSpinner(false);
        setErrorMessage(error.response.data.message);
        setError(true);
        setTimeout(() => {
          setError(false);
          setErrorMessage('');
        }, 8000);
      }
      );
  }, [request])

  const logout = () => {
    localStorage.clear();
    setPageRefresh('1');
  }



  const onSubmitHandler = async (event) => {
    event.preventDefault();

    if (newMessage !== '') {
      const localStorageUser = localStorage.getItem('user');
      const user = JSON.parse(localStorageUser);
      const newUserMessage = {
        message: newMessage,
        userId: user.user._id,
        userImageUrl: user.user.imageUrl,
        userName: user.user.name + " " + user.user.lastName,
      }
      setNewMessage('');
      try {
        const result = await axios.post('/api/newmessage', newUserMessage);
        setChat([...chat, { message: newMessage, _id: Math.random().toString() }])
      } catch (error) {
        setSpinner(false);
        setErrorMessage(error.response.data.message);
        setError(true);
        setTimeout(() => {
          setError(false);
          setErrorMessage('');
        }, 8000);
      }

    }
  }

  const allMessages = chat.map(ch => {
    return (
      <List message={ch} key={ch._id} />
    )
  });





  return (
    <>
      {
        spinner ? <Spinner /> : ''
      }
      <AppBar logout={logout} />
      {
        error ? <Alert message={errorMessage} /> : null
      }
      <Grid container className={classes.root} spacing={2}>
        <Grid container justifyContent="center" spacing={spacing}>
          <Grid item>
            <Paper className={classes.paper}>
              <SmallAppBar />
              <User />
            </Paper>
          </Grid>
          <Grid item>
            <Paper className={classes.paper}>
              <div id='paperDiv' className={classes.messageDiv}>
                {allMessages}
              </div>
              <form onSubmit={onSubmitHandler} >
                {/* <label>Enter Message</label> */}
                <input placeholder='Enter your message:' className={classes.input} value={newMessage} onChange={event => setNewMessage(event.target.value)} />
                <button className={classes.button}>Submit</button>
              </form>
            </Paper>
          </Grid>
        </Grid>
      </Grid>
      )
    </>
  );
}





export default SpacingGrid